export default {
	data() {
		return {
			wordnumer: 0,
			id: '',
			bustext: '',
			windowHeight: '',
		}
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;
		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		getinfo: function() {
			var that = this;
			uni.request({
				url: that.serveipd + '/api/merchant/auth/info/' + that.id,
				method: 'GET',
				data: {
					id: that.id
				},
				success: res => {
					console.log(res.data.data.storeIntroduction);
					that.bustext = res.data.data.storeIntroduction;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		bindTextAreaBlur: function(e) {
			var that = this;
			that.wordnumer = e.target.value.length;
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		preserve: function() {
			var that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/auth/introduction',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				data: {
					id: parseInt(that.id),
					storeIntroduction: that.bustext,
				},
				success: res => {
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1500
					});
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
